.custom-profile-chart-container {
    width: 100%;
    height: 550px;
}

.chart-titles-container {
    width: 100%;
    list-style-type: none;
    padding-left: 0px;
    display: flex;
    flex-direction: row;
    row-gap: 10px;
    align-items: center;
    justify-content: space-around;
    position: sticky;
    top: 0px;
    padding: 25px;
}

.chart-title {
    width: calc(50% - 1px);
    column-gap: 50px;
    text-align: center;
    font-weight: 900;
    font-size: 16px;
    cursor: pointer;
}

.custom-profile-chart-content {
    width: 100%;
    list-style-type: none;
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    align-items: center;
    height: 500px;
    overflow: auto;
    padding: 15px;
}

.data-container {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    column-gap: 50px;
    cursor: pointer;
    height: 70px;
}

.main-data-container {
    height: 40px;
    width: calc(50% - 1px);
}

.main-data {
    height: 100%;
    border-radius: 2px;
    margin-left: auto;
    background-color: black;

}

.data-label-name {
    /* width: calc(10% - 1px); */
    width: 120px;
    white-space: nowrap;
    text-align: center;
    margin-left: 10px;
    margin-right: 10px;
}

.secondary-data-container {
    width: calc(50% - 1px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    row-gap: 10px;
}

.secondary-frequency {
    height: 30px;
    box-shadow: rgba(14, 30, 37, 0.12) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
    margin-right: auto;
    border-radius: 2px;
}

.secondary-illustration-container {
    display: flex;
    column-gap: 5px;
    width: 100%;
    height: 100px;
    justify-content: flex-start;
    align-items: center;
}

.secondary-illustration {
    border-radius: 50%;
    width: 30px;
    height: 30px;
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    border: 1px solid rgba(49, 51, 63, 0.2);
}

.chart-title-container {
    display: flex;
    align-items: center;
    height: 100%;
}

.title-container {
    list-style-type: none;
    padding-left: 0px;
    height: 100%;
    /* height: 750px; */
    display: flex;
    flex-direction: column;
    /* justify-content: center; */
    justify-content: space-around;
}

.title {
    width: fit-content;
    white-space: nowrap;
    font-weight: 900;
    margin-right: 3px;
}

.custom-profile-chart-content-hor {
    display: flex;
    list-style-type: none;
    padding-left: 0px;
    column-gap: 12px;
    height: 550px;
    overflow: auto;
}

.data-container-hor {
    display: flex;
    flex-direction: column;
    height: 100%;
    cursor: pointer;
}

.main-data-container-hor {
    width: 100%;
    display: flex;
    align-items: flex-end;
    height: calc(50% - 1px);
}

.main-data-hor {
    background-color: black;
    border-radius: 2px;
    width: 60px;
    display: flex;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
}

.data-label-name-hor {
    height: 80px;
    /* width: 120px; */
    margin: auto;
    white-space: nowrap;
    text-align: center;
    line-height: 80px;
    margin-top: 10px;
    margin-bottom: 10px;
}

.secondary-data-container-hor {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    height: calc(50% - 1px);
    justify-content: center;

}

.secondary-illustration-container-hor {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 10px;
}

.secondary-illustration-hor {
    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
    border-radius: 50%;
    border: 1px solid rgba(49, 51, 63, 0.2);
}

@media (max-width:1440px) {

    .chart-title {
        font-size: 11px;
    }

    .data-label-name {
        width: 55px;
        font-size: 12px;
    }

    .data-container {
        column-gap: 30px;
    }

    .title {
        width: fit-content;
        white-space: nowrap;
        font-weight: 900;
        font-size: 11px;
        margin-right: 1px;
    }

    .data-label-name-hor {
        font-size: 12px;
        margin: auto;
    }
}

@media (max-width: 502px) {

    .data-label-name {
        font-size: 8.5px;
    }

    .data-container {
        column-gap: 10px;
    }

    .title {
        font-size: 8.5px;
    }

    .data-container-hor {
        width: 50px;
    }

    .data-label-name-hor {
        font-size: 9px;
    }

}




::-webkit-scrollbar {
    width: 5px;
    height: 5px;
}

/* Track */
::-webkit-scrollbar-track {
    background: white;

}

/* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 99px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555;
}